* {
	box-sizing: border-box;
	&:before, &:after {
		box-sizing: border-box; } }

html, body {
	height: 100%;
	width: 100%; }

body {
	font-size: 12px;
	min-width: 320px;
	position: relative;
	font-family: Georgia, 'Times New Roman', Times, serif;
	overflow-x: hidden;
	opacity: 1;
	padding: 0;
	margin: 0; }

.img-responsive {
	display: block;
	max-width: 100%;
	height: auto; }

.text-center {
	text-align: center; }
