html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

* {
  box-sizing: border-box;
}

*:before, *:after {
  box-sizing: border-box;
}

html, body {
  height: 100%;
  width: 100%;
}

body {
  font-size: 12px;
  min-width: 320px;
  position: relative;
  font-family: Georgia, 'Times New Roman', Times, serif;
  overflow-x: hidden;
  opacity: 1;
  padding: 0;
  margin: 0;
}

.img-responsive {
  display: block;
  max-width: 100%;
  height: auto;
}

.text-center {
  text-align: center;
}

@media screen and (max-width: 975px) {
  body {
    font-size: 20px;
  }
  .wrapper {
    padding-top: 0;
  }
  .news_second {
    grid-template-columns: repeat(2, 1fr);
  }
  .news_article-tags {
    width: 200px;
  }
  .news_article-social {
    width: 60px;
  }
  .news_article_social-link {
    width: 22px;
    height: 22px;
  }
  .news_hot-text {
    width: 100%;
  }
  .article_img {
    padding-bottom: 100%;
    padding-left: 100%;
  }
  .content_head {
    flex-direction: column;
  }
  .content_head-label {
    width: 100%;
    text-align: center;
  }
  .content_head-text {
    padding: 5px;
    text-align: center;
  }
  .content_head-text::before {
    border: none;
  }
  .comments::before {
    width: 24px;
    height: 18px;
    top: 2px;
  }
  .header_terms,
  .main_nav {
    display: none;
  }
  .header_main {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  .header_hottest {
    display: none;
  }
  .mobile_menu {
    padding: 15px 0;
    font-size: 1.2em;
    z-index: 4;
    display: block;
  }
  .mobile_menu .header_terms,
  .mobile_menu .main_nav {
    display: block;
    color: inherit;
  }
  .hamburger_wraper {
    display: inline-block;
    position: relative;
    top: 60px;
  }
  .links > li {
    display: block;
    color: #2e2e2e;
  }
}

@media screen and (max-width: 800px) {
  body {
    font-size: 28px;
  }
  .header_hottest {
    text-align: center;
  }
  .content {
    padding: 0;
  }
  .news_second {
    grid-template-columns: repeat(1, 1fr);
  }
  .news_hot {
    padding-bottom: 0;
    padding-right: 0;
    padding-left: 0;
  }
  .news_hot-text {
    width: 100%;
  }
  .news_article {
    flex-direction: column;
  }
  .news_article-label {
    padding-bottom: 100%;
    margin-right: 0;
  }
  .news_article-additional > .comments {
    margin-left: 50px;
  }
  .news_article-tags {
    display: none;
  }
  .news_article-social {
    width: 100%;
  }
  .news_article_social-link {
    width: 30px;
    height: 30px;
  }
  main {
    flex-direction: column;
  }
  .hottest_video-aside {
    text-align-last: left;
  }
  .hottest_video-article {
    margin-bottom: 90px;
    text-align: center;
  }
  .hottest_video-img {
    width: 100%;
    padding-bottom: 100%;
  }
  .hottest_video-comments {
    margin-left: 50px;
  }
  .comments::before {
    width: 30px;
    height: 24px;
    right: 35px;
    top: 6px;
  }
}

.wrapper {
  padding: 0;
  padding-top: 35px;
  margin: 0;
  background-image: url(../img/bg.jpg);
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;
}

.container {
  margin: 0 auto;
  margin-bottom: 0;
  max-width: 1000px;
  border-radius: 5px;
  overflow: hidden;
}

.header_hottest {
  background: linear-gradient(#f4f5f6, #e3e3e3);
  padding: 2px 30px;
}

.header_hottest-label {
  background-color: #85afbd;
  text-transform: uppercase;
  font-size: 1.1666667em;
  color: #fff;
  padding: 0 5px;
  border-radius: 3px;
  display: inline-block;
  margin-right: 10px;
  font-family: 'Barlow Condensed', sans-serif;
  line-height: 1.5em;
}

.header_hottest-head {
  display: inline-block;
  font-family: Georgia, 'Times New Roman', Times, serif;
  font-style: italic;
  color: #2b5e5e;
  font-size: 0.9166667em;
  text-decoration: none;
  padding-top: 10px;
  padding-bottom: 10px;
}

.header_hottest-head:hover {
  text-decoration: underline;
}

.header_main {
  background-image: url(../img/header_bg.jpg);
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;
  padding: 16px 20px;
}

.header_links {
  display: inline-block;
  padding: 10px;
  padding-left: 20px;
  vertical-align: top;
}

.header_terms {
  margin-bottom: 15px;
  padding-left: 13px;
}

.header_terms-link {
  text-decoration: none;
  color: #a9a9a9;
  font-size: 1.1666667em;
}

.header_terms-link:hover {
  text-decoration: underline;
}

.header_terms > .links > li {
  margin-right: 10px;
}

.logo {
  display: inline-block;
  width: 146px;
  height: 75px;
  margin-top: 15px;
  margin-left: 10px;
  margin-bottom: 7px;
  background-image: url(../img/logo.png);
  background-repeat: no-repeat;
  background-size: cover;
}

.links {
  margin: 0;
  padding: 0;
  list-style: none;
  font-family: 'Barlow Condensed', sans-serif;
  text-transform: uppercase;
}

.links > li {
  display: inline-block;
  color: #f6f6f6;
}

.main_nav-link {
  text-decoration: none;
  color: inherit;
  font-size: 1.5em;
  padding: 10px 13px;
  border-radius: 3px;
}

.main_nav-link:hover {
  box-shadow: inset 0 0 1px 1px rgba(0, 0, 0, 0.3);
  background-color: #092f3b;
}

main {
  display: flex;
  flex-direction: row;
  padding: 30px;
  margin: 0;
  background-color: #ededed;
}

.content {
  padding-right: 32px;
}

.content_head {
  display: flex;
  flex-direction: row;
  align-items: center;
  background-color: #cacaca;
  padding: 0;
  margin-bottom: 5px;
}

.content_head-label {
  margin: 0;
  display: block;
  padding: 7px 13px;
  padding-right: 35px;
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
  color: #f6f6f6;
  font-size: 1.5em;
  text-transform: uppercase;
  font-family: 'Barlow Condensed', sans-serif;
  font-weight: 400;
  text-decoration: none;
  background-color: #ae0002;
}

.content_head-label:hover {
  text-decoration: underline;
}

.content_head-text {
  margin: 0;
  padding-left: 35px;
  position: relative;
  font-family: Georgia, 'Times New Roman', Times, serif;
  color: #2e2e2e;
}

.content_head-text::before {
  content: '';
  margin: 0;
  display: block;
  height: 100%;
  border: 18px solid transparent;
  border-left-color: #ae0002;
  position: absolute;
  left: 0px;
  top: -11px;
}

.news_hot {
  background-repeat: no-repeat;
  background-position: top left;
  padding: 96px 40px 20px 40px;
  background-size: cover;
  margin-bottom: 15px;
}

.news_hot-text {
  width: 340px;
  padding: 16px 25px 25px 17px;
  color: #e2e2e2;
  background-color: #1e1e1e;
  line-height: 1.5em;
  text-align: justify;
}

.news_second {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 15px;
  margin-bottom: 35px;
}

.news_article {
  display: flex;
  flex-direction: row;
}

.news_article-block {
  padding-bottom: 15px;
}

.news_article-text {
  color: #757575;
  line-height: 1.5em;
  margin-bottom: 11px;
  padding-right: 5px;
}

.news_article-label {
  background-size: cover;
  display: block;
  height: auto;
  min-width: 170px;
  margin-right: 25px;
  background-repeat: no-repeat;
  background-position: center center;
}

.news_article-additional {
  display: flex;
  flex-direction: row;
  align-items: baseline;
}

.news_article-additional > .comments {
  margin-right: 20px;
  margin-left: 25px;
}

.news_article-tags {
  color: #989898;
  margin-right: 30px;
}

.news_article-tags-head {
  margin-right: 10px;
}

.news_article-social {
  color: #989898;
}

.news_article_tag-link {
  color: #2b5e5e;
  font-style: italic;
}

.news_article_tag-link:hover {
  text-decoration: none;
}

.news_article_social-link {
  background-repeat: no-repeat;
  background-size: cover;
  width: 16px;
  height: 16px;
  display: inline-block;
  margin-right: 5px;
  position: relative;
  top: 3px;
}

.news_top-video {
  margin-bottom: 30px;
}

.news_top-video > .content_head {
  margin-bottom: 25px;
}

.facebook {
  background-image: url(../img/facebook.png);
}

.twitter {
  background-image: url(../img/twitter.png);
}

.article {
  background-color: #fff;
  padding: 15px;
  border-radius: 3px;
  box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.14);
  will-change: box-shadow;
}

.article:hover {
  box-shadow: 1px 1px 5px 5px rgba(0, 0, 0, 0.14);
  transition: .3s box-shadow ease-in-out;
}

.article_img {
  display: block;
  width: auto;
  min-height: 120px;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  margin-bottom: 12px;
}

.article_head {
  color: #2e2e2e;
  text-decoration: underline;
  line-height: 1.5em;
}

.article_head:hover {
  text-decoration: none;
}

.article_text-preview {
  font-size: 0.9166667em;
  line-height: 1.5em;
  font-style: italic;
  color: #757575;
  margin-bottom: 0;
  margin-top: 7px;
}

.comments {
  display: block;
  font-style: italic;
  font-size: 0.9166667em;
  color: #989898;
  text-align: right;
  position: relative;
  text-decoration: none;
  cursor: pointer;
}

.comments:hover {
  text-decoration: underline;
}

.comments::before {
  content: '';
  display: block;
  width: 17px;
  height: 13px;
  background-image: url(../img/comments.png);
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  right: 20px;
}

.hottest_video {
  min-width: 186px;
}

.hottest_video-article {
  margin-bottom: 22px;
}

.hottest_video-head {
  text-decoration: none;
  margin-bottom: 5px;
  border-radius: 3px;
}

.hottest_video-head:hover {
  text-decoration: underline;
}

.hottest_video-img {
  display: block;
  width: auto;
  min-height: 175px;
  background-size: contain;
  background-repeat: no-repeat;
  margin-bottom: 10px;
  position: relative;
}

.hottest_video-aside {
  text-decoration: underline;
  color: #2e2e2e;
  line-height: 1.5em;
}

.hottest_video-aside:hover {
  text-decoration: none;
}

.hottest_video-text {
  font-style: italic;
  font-size: 0.9166667em;
  line-height: 1.5em;
  color: #757575;
  margin-top: 10px;
}

.hottest_video-comments {
  display: inline-block;
  margin-left: 30px;
}

.hottest_video-comments::before {
  position: absolute;
  top: 3px;
}

.hottest_video_sign {
  display: block;
  width: 35px;
  height: 35px;
  position: absolute;
  bottom: 10px;
  left: 5px;
  background-image: url(../img/video_sign.jpg);
  background-repeat: no-repeat;
  background-position: bottom left;
}

.mobile_wraper {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.95);
  z-index: 3;
  display: block;
  position: fixed;
  overflow-y: scroll;
}

.mobile_menu {
  width: 100vw;
  position: absolute;
  top: -700px;
  z-index: 2;
  display: none;
}

.mobile_menu .links {
  display: block;
}

.hamburger_wraper {
  cursor: pointer;
  width: 40px;
  display: none;
  margin-top: -15px;
  z-index: 5;
}

.hamburger_item,
.hamburger_item::before,
.hamburger_item::after {
  display: block;
  width: 100%;
  height: 6px;
  background-color: #989898;
  border-radius: 3px;
}

.hamburger_item::before,
.hamburger_item::after {
  content: '';
  position: relative;
}

.hamburger_item::before {
  top: -12px;
}

.hamburger_item::after {
  bottom: -6px;
}

.is-active {
  top: 35px;
  transition: top .3s ease-in;
}

.menu-open .hamburger_item,
.menu-close .hamburger_item {
  transition: background-color .3s ease-in;
}

.menu-open .hamburger_item {
  background-color: #989898;
}

.menu-close .hamburger_item {
  background-color: transparent;
}

.menu-close .hamburger_item,
.menu-open .hamburger_item,
.menu-close .hamburger_item::before,
.menu-close .hamburger_item::after,
.menu-open .hamburger_item::before,
.menu-open .hamburger_item::after {
  transition: all .3s ease-in;
}

.menu-open .hamburger_item::before {
  top: -12px;
}

.menu-close .hamburger_item::before {
  transform: rotate(-45deg);
  top: 3px;
}

.menu-open .hamburger_item::after {
  bottom: -6px;
}

.menu-close .hamburger_item::after {
  transform: rotate(45deg);
  top: -3px;
}

.disabled {
  transition: all .3s ease-in;
}
